import {useState, useEffect} from "react";
import Ciudad from './Ciudad/Ciudad';

function CiudadesBuscadas(props) {
  
    const [coordenadas, setCoordenadas] = useState([]);
    const [coordenadasCargadas, setCoordenadasCargadas] = useState(false);

    const [coordenadasConPaises, setCoordenadasConPaises] = useState(new Map());
    const [coordenadasConPaisesCargadas, setCoordenadasConPaisesCargadas] = useState(false);

    const [contenidoCiudad, setContenidoCiudad] = useState();


    const ciudades = props.ciudades;
    const cargando = props.cargando;
    let keyID = 0;

    const acumularCoordenadasDeCiudades = () =>{
        let coordenadasParaAcumular = [];
        
        ciudades.list.forEach((ciudad)=>{
            let coordenadaConjunta = (Math.round((Math.abs(ciudad.coord.lat) + Math.abs(ciudad.coord.lon)) * 100))/100;
        
            coordenadasParaAcumular = [...coordenadasParaAcumular, coordenadaConjunta];
            
        })
        setCoordenadasCargadas(true);
        return coordenadasParaAcumular;
    }

    const acumularCoordenasConPaisesDeCiudades = () =>{
        const paisesConCoordenadas = new Map();
        
        ciudades.list.forEach((ciudad , index)=>{
        paisesConCoordenadas.set(coordenadas[index], ciudades.list[index]);
        });

        const paisesConCoordenadasOrdenado = new Map([...paisesConCoordenadas].sort((a,b) => a[0] - b[0]));

        return paisesConCoordenadasOrdenado;
    }

    useEffect(()=>{

        const coordenadasAcumuladas = acumularCoordenadasDeCiudades();
        setCoordenadas(coordenadasAcumuladas);

    // eslint-disable-next-line 
    }, [ciudades])
    


    useEffect(()=>{
        if(coordenadasCargadas){
            const coordenasConPaisesDeCiudadesAcumulados = acumularCoordenasConPaisesDeCiudades();
            setCoordenadasConPaises(coordenasConPaisesDeCiudadesAcumulados);
        }

    // eslint-disable-next-line 
    }, [coordenadas]);


    useEffect(()=>{
        if(coordenadasConPaises.size !== 0){
            setCoordenadasConPaisesCargadas(true);
        }
    // eslint-disable-next-line 
    }, [coordenadasConPaises]);



    return (
        <div>
            {cargando && coordenadasConPaisesCargadas? (
            <h1>Cargando</h1>
            ) : (
            <div>
                {
                // eslint-disable-next-line
                ciudades.list.map((datosCiudad, indexMap) => {
                    keyID += 1;   
                    const coordenadasConPaisesExtendidas = [...coordenadasConPaises];

                    
                    return (
                        <Ciudad
                            key={keyID}
                            datos={datosCiudad}
                            levantarDatosCiudad={props.levantarDatosCiudad}
                            cargando={cargando}
                        ></Ciudad>
                    );  
                })}
            </div>
            )}
        </div>
    );
}

export default CiudadesBuscadas;
